import back from '@/components/back'
import lrz from 'lrz'

export default {
    components:{
        back
    },
    data(){
        return{
            showPopup: false,
            ReportImageType: [],
            Diagnosis: '',
            Operation: '',
            DiagnosisArr: [],
            OperationArr: [],
            hj: [],
            CT: [],
            DiseaseDesc: '',
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
            OperationTime: ''
        }
    },
    created(){
        this.pull()
    },
    methods:{
        pull(){
            this.$get('/PlatFormAPI/Category/QueryItemByEnum',{
                categoryEnum:25
            }).then(o => {
                this.OperationArr = o
            })
            this.$get('/PlatFormAPI/DiseaseGroupAndRule/QueryDiseaseGroupForMedical').then(o => {
                this.DiagnosisArr = o
            })
        },
        getImg(type){
            this.nowTarget = type
        },
        toPreview(arr,idx){
            const data = {
                position: idx,
                url: arr
            }
            this.$bridge.callhandler('Preview', JSON.stringify(data))
        },
        update(e){
            const that = this
            const file = e.target.files[0]
            if (file && this.nowTarget){
                lrz(file).then(rst => {
                    var reader = new FileReader();
                    reader.readAsDataURL(file);
                    const that = this
                    reader.onload = function (e) {
                        that.er = this.result.split('base64,')[1]
                        that.$post('PlatFormAPI/PatientArchives/ReportImageUpload',{
                            Base64Str: this.result.split('base64,')[1]
                        }).then(o => {
                            that[that.nowTarget].push(o)
                        })
                    }
                })
                e.target.value = null
            }
        },
        pop(target,idx){
            this[target].splice(idx,1)
        },
        send(){
            /*if(this.DiseaseDesc.length < 10){
                return this.$toast('请输入至少10个字的病情描述')
            }*/
            if(this.Operation && this.Operation.CategoryItemValue !== 0 && !this.OperationTime){
                return this.$toast('请选择手术时间')
            }
            const ImgDetails = []
            this.hj.forEach(v => {
                ImgDetails.push({
                    ImagePaths: v.SavePath,
                    ImageType: 3,
                    PictureLabel: '喉镜'
                })
            })
            this.CT.forEach(v => {
                ImgDetails.push({
                    ImagePaths: v.SavePath,
                    ImageType: 3,
                    PictureLabel: 'CT、MR、DR'
                })
            })
            let DisGroupList = [
                {
                    DisGroupId: this.Diagnosis.DisGroupId,
                    DisGroupName: this.Diagnosis.DisGroupName
                }
            ]
            this.$post('PlatFormAPI/PatientArchives/ReportImagSave',{
                DiseaseDesc: this.DiseaseDesc,
                Diagnosis: this.Diagnosis.DisGroupName,
                OperationName: this.Operation.CategoryItemName,
                OperationType: this.Operation.CategoryItemValue,
                MedicalTempId: 2,
                PatientId: this.orderInfo.PatientInfo.PatientId,
                PatientName: this.orderInfo.PatientInfo.PatientName,
                UserType: 2,
                CreateId: this.user.Data.User.DoctorId,
                CreateName: this.user.Data.User.DoctorName,
                OperationTime: this.OperationTime,
                ImgDetails,
                DisGroupList
            }).then(o => {
                this.$toast('上传成功')
                this.$router.back()
            })
        }
    }
}



